import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Chat from '@/components/Chat'
import User from '@/components/User'

Vue.use(Router)

export default new Router({
  routes: [
    {
		path: '/app/',
		name: 'User',
		props: true,
		component: User
    },

    {
    	path: '/chat/',
    	name: 'Chat',
    	component: Chat,
    	props: true,
    }
  ],

  mode: 'history',
})



