<template>
  <div id="app">

    <div class="phone-viewport">
      <md-toolbar>
        <md-button class="md-icon-button" @click="toggleLeftSidenav">
          <md-icon>menu</md-icon>
        </md-button>

        <h2 class="md-title">Realtime Chat</h2>
      </md-toolbar>

      <md-sidenav class="md-left" ref="leftSidenav" @open="open('Left')" @close="close('Left')">
        <md-toolbar class="md-small">
          <div class="md-toolbar-container">
            <h3 class="md-title">Current Users</h3>
          </div>
         </md-toolbar>

        <div class="users_list">
          <md-button v-show="isAdmin" type="submit" @click="clearChat" class="md-raised md-primary">Clear</md-button>

          <ul>
            <li v-for="user in orderedMessages"><md-icon>account_circle</md-icon> {{ user.username }}</li>
          </ul>
        </div>
           
      </md-sidenav>
    </div>

    <div class="chatbox">
      <router-view :conversations="orderedMessages"></router-view>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import Chat from './components/Chat';

  export default {
    components: {
      'app-chat': Chat,
    },

    name: 'app',

    data() {
      return {
        conversations: [],
        isAdmin: false,
      }
    },

    ready: function() {
    },

    watch: {
      checkAdmin() {
        this.isAdmin = !this.isAdmin;
      },  

      verifyCookie: function() {
        let cookieVal = document.cookie;
        
        if(cookieVal) {
          this.$router.push('/chat/')
        } else {
          this.$router.push('/app/')
        }
      },
    },

    mounted: function() {
      this.checkCookie()
    },

    created: function() {
      this.getMessages()
      this.verifyCookie()
    },

    computed: {
      orderedMessages: function () {
        return _.orderBy(this.conversations, ['date_message', 'username'], ['desc', 'asc']);
      },

      checkAdmin() {
        let cookieVal = document.cookie;
        if(cookieVal == 'reece') {
          this.isAdmin = !this.isAdmin;
        }
      },      
    },

    methods: {
      checkCookie: function() {
        console.log('test ' + document.cookie )  
      },

      verifyCookie: function() {
        let cookieVal = document.cookie;
        
        if(cookieVal) {
          this.$router.push('app/chat/')
        } else {
          this.$router.push('/app')
        }
      },

      clearChat() {
        axios.delete('https://vue-rb-chat.firebaseio.com/conversations.json')
          .then(response => {
            console.log('chat cleared!')
          })
          .catch(e => {
            console.log(e)
          })
      },      

      getMessages() {
        axios.get('https://vue-rb-chat.firebaseio.com/conversations.json')
          .then((response) => {
            let messageArray = [];
            this.conversations = response.data;
            for(let key in this.conversations) {
              this.conversations[key].id = key;
              messageArray.push(this.conversations[key])
            }
            this.conversations = messageArray;
          })
        setTimeout(this.getMessages, 500);
      },

      toggleLeftSidenav() {
        this.$refs.leftSidenav.toggle();
      },
      
      toggleRightSidenav() {
        this.$refs.rightSidenav.toggle();
      },

      closeRightSidenav() {
        this.$refs.rightSidenav.close();
      },

      open(ref) {
        console.log('Opened: ' + ref);
      },

      close(ref) {
        console.log('Closed: ' + ref);
      },
    },

  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  ul {
    list-style-type: none;
  }

  ul li {
    display: block;
    text-align: left;
  }
</style>



