<template>
	<div class="conversations">
		<md-layout md-gutter>
			<md-layout></md-layout>
			<md-layout md-flex="80">
				<md-input-container>
					<md-input
						class="user" 
						v-model="textMessage" 
						@keyup.enter.native="sendChat" 
						placeholder="Please type your MESSAGE and PRESS ENTER">
					</md-input>
				</md-input-container>

				<div class="messagelist">
					<ul>
						<li v-for="chat in conversations"><strong>{{ chat.username }}:</strong> {{ chat.message }} <small>{{ dateMessage(chat.date_message) }}</small> </li>
					</ul>
				</div>
			</md-layout>
			<md-layout></md-layout>
		</md-layout>
	</div>
</template>

<script>
	import axios from 'axios';
	
	export default {
		props: {
		 	conversations: {
		 		type: Array,
			}
		},

		data() {
			return {
				msg: 'Chatbox',
				textMessage: '',
				messageDate: '',
			}
		},

		created: function() {
			console.log(document.cookie);
		},

		mounted: function() {

		},

		computed: {
			dateNow() {
				return this.$moment();
			},
		},

		methods: {

			dateMessage(x) {
				return this.$moment(x).format("YYYY-MM-DD h:mm:ss A");
			},

			sendChat() {
			    axios.post('https://vue-rb-chat.firebaseio.com/conversations.json', {
			    	id: 1,
			        username: document.cookie,
			        message: this.textMessage,
			        date_message: this.dateNow,
			      })
			      .then(response => {})
			      .catch(e => {
			        this.errors.push(e)
			      }) 
			}
		},
	}

</script>

<style scoped>
	.conversations {
		height: 30vh;
	    justify-content: center;
	    align-items: center;
	    display: flex;
	}

	small {
		font-size: 9px;
	}

	ul {
		list-style-type: none !important;
		padding-left: 0 !important;
	}

	ul li {
		display: block;
		text-align: left;
	}
</style>


