<template>
	<div class="user-name">
		<md-layout md-gutter>
			<md-layout></md-layout>
			<md-layout md-flex="50" class="enterchatwrapper">
				<div v-show="isOk" class="errorMsg">
					<h5><md-icon>error_outline</md-icon> Please enter your username!</h5>
				</div>
				<md-input-container>
					<md-input
						class="user" 
						v-model="user" 
						@keyup.enter.native="enterChat" 
						placeholder="Please type your USERNAME and PRESS ENTER">
					</md-input>
				</md-input-container>
			</md-layout>
			<md-layout></md-layout>
		</md-layout>
	</div>
</template>

<script>

	export default {
		data() {
			return {
				user: '',
				isOk: false,
			}
		},

		mounted: function() {

		},

		methods: {
			enterChat() {
				if(this.user == '') {
					this.isOk = true;
				} else {
					let x = document.cookie = this.user;
					this.$router.push('/chat/')
					return x;					
				}
			}
		},
	}

</script>

<style>
	.enterchatwrapper {
		justify-content: center;
		align-items: center;
		display: flex;
	}

	.errorMsg {
		text-align: center;
		color: red;
	}

	.user-name {
		height: 60vh;
		justify-content: center;
		align-items: center;
		display: flex;
	}

	.user {
		text-align: center;
	}
</style>


